.contentContainer {
  .top,
  .bottom {
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;
    margin: 20px;
    display: flex;

    h1 {
      color: lightgray;
      font-size: 20px;
    }

    form {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
      justify-content: space-between;
      padding: 0 30px;

      .formInput {
        width: 45%;

        label {
          display: flex;
          align-items: center;
          gap: 10px;

          .icon {
            cursor: pointer;
          }
        }

        input {
          width: 100%;
          padding: 5px;
          border: none;
          border-bottom: 1px solid gray;
        }
      }

      button {
        width: 150px;
        padding: 10px;
        border: none;
        background-color: teal;
        color: #fff;
        font-weight: bold;
        cursor: pointer;
        margin-top: 10px;
      }
    }
  }
}
